import React, { useState, useRef } from "react";
import { Dots, OptimizedSlider, SliderValues } from "components";
import { ReactComponent as Arrow } from "assets/icons/sliderArrow.svg";
import SliderText from "./SliderText";
import css from "./style.module.scss";
import { useSliderData } from "./config";

export const SliderBlock = () => {
  const [activeIndex, setActiveIndex] = useState(0);
  const sliderData = useSliderData();
  const sliderRef = useRef<SliderValues | null>(null);

  const increment = () => {
    sliderRef.current?.increment();
  };
  const decrement = () => {
    sliderRef.current?.decrement();
  };

  return (
    <div className={css.root}>
      <div className={css.leftContainer}>
        <Arrow onClick={decrement} className={css.arrow} />
      </div>

      <OptimizedSlider
        dataLength={sliderData.length}
        ref={sliderRef}
        cardsAmount={1}
        setActualIndex={setActiveIndex}>
        {sliderData.map(({ img, title, text }) => (
          <div className={css.cardContainer} key={title}>
            <img src={img} alt={title} className={css.img} />
            <Dots actual={activeIndex + 1} amount={sliderData.length} />
            <SliderText title={title} text={text} />
          </div>
        ))}
      </OptimizedSlider>

      <div className={css.rightContainer}>
        <Arrow className={css.arrowRight} onClick={increment} />
      </div>
    </div>
  );
};
