import React, { useEffect, useState } from "react";
import { useFetch, useLocalStorage } from "hooks";

import "config/themes/_wager.scss";

import { fetchBase } from "utils";

import { FetchProfileData, ProfileData } from "pages/ModalPages/pages/Profile";

import {
  HeaderBlock,
  InfoBlock,
  Rights,
  VipModal,
  UserModal,
} from "./components";
import css from "./style.module.scss";
import { Helmet } from "react-helmet";

interface LevelItemFetch {
  message: string;
  success: boolean;
  levels: LevelItem[];
}
export interface LevelItem {
  credit_amount: string;
  level: string;
  total_wagered_required: number;
}

const Levels = () => {
  const [token] = useLocalStorage("token");
  const [levelsFetch, { data }] = useFetch<LevelItemFetch>("statistics/levels");

  const [profile, setProfile] = useState<ProfileData | undefined>();
  const [levelsList, setLevelsList] = useState<LevelItem[]>([]);
  const [openVipModal, setOpenVipModal] = useState(false);
  const [openUserModal, setOpenUserModal] = useState(false);

  const handleVipModalOpen = () => setOpenVipModal(true);
  const handleVipModalClose = () => setOpenVipModal(false);

  const handleUserModalOpen = () => setOpenUserModal(true);
  const handleUserModalClose = () => setOpenUserModal(false);

  const getProfile = async () => {
    const response = await fetchBase<FetchProfileData>("statistics/profile");

    if (response.data) {
      setProfile(response.data.profile);
    }
  };

  useEffect(() => {
    getProfile();
  }, [token]);

  useEffect(() => {
    levelsFetch();
  }, []);

  useEffect(() => {
    if (data) {
      setLevelsList(data.levels);
    }
  }, [data]);

  return (
    <>
      <Helmet>
        <title>Level Dashboard | Crypto Gambling Casino</title>
      </Helmet>

      <div className={css.root}>
        <HeaderBlock />
        <InfoBlock
          handleOpenVipModal={handleVipModalOpen}
          handleOpenUserModal={handleUserModalOpen}
          profile={profile}
          levels={levelsList}
        />
        <Rights />

        <VipModal
          open={openVipModal}
          onClose={handleVipModalClose}
          list={levelsList}
          profile={profile}
        />

        <UserModal
          open={openUserModal}
          onClose={handleUserModalClose}
          profile={profile}
        />
      </div>
    </>
  );
};

export default Levels;
