import React from "react";

import { default as MedalSlider } from "pages/ModalPages/pages/Profile/Medals";

import css from "./style.module.scss";
import { useUser } from "providers";

export const Medals = () => {
  const { user } = useUser();

  return (
    <div className={css.root}>
      <MedalSlider user={user?.user_name || ""} />

      <div className={css.background} />
    </div>
  );
};
