import React, { FC, useMemo } from "react";

import css from "./style.module.scss";
import { ProfileData } from "pages/ModalPages/pages/Profile";
import { medalUrlConfig } from "assets/levels";

interface Props {
  profile?: ProfileData;
}

const Medal: FC<Props> = ({ profile }) => {
  const medalUrl = useMemo(() => {
    if (!profile) {
      return medalUrlConfig["60"];
    }
    const N = Number;

    const sortedMedals = Object.entries(medalUrlConfig)
      .sort((a, b) => N(a[0]) - N(b[0]))
      .reverse();

    for (const [key, url] of sortedMedals) {
      if (N(profile.level) >= N(key)) {
        return url;
      }
    }

    return medalUrlConfig["0"];
  }, [profile]);

  return <img src={medalUrl} alt="emblem" className={css.img} />;
};

export default Medal;
