import { useGetLocale } from "providers";
import Medal_1URL from "./medals/medal_1.svg";
import Medal_23URL from "./medals/medal_23.svg";
import Medal_32URL from "./medals/medal_32.svg";
import Medal_46URL from "./medals/medal_46.svg";
import Medal_58URL from "./medals/medal_58.svg";
import Medal_64URL from "./medals/medal_64.svg";
import Medal_70URL from "./medals/medal_70.svg";

interface SliderDataType {
  img: string;
  title: string;
  text: string;
}

export const useSliderData = (): SliderDataType[] => {
  const intl = useGetLocale();

  return [
    {
      img: Medal_1URL,
      title: intl.formatMessage({ id: "level.slider.m_01.title" }),
      text: intl.formatMessage({ id: "level.slider.m_01.text" }),
    },
    {
      img: Medal_23URL,
      title: intl.formatMessage({ id: "level.slider.m_02.title" }),
      text: intl.formatMessage({ id: "level.slider.m_02.text" }),
    },
    {
      img: Medal_32URL,
      title: intl.formatMessage({ id: "level.slider.m_03.title" }),
      text: intl.formatMessage({ id: "level.slider.m_03.text" }),
    },
    {
      img: Medal_46URL,
      title: intl.formatMessage({ id: "level.slider.m_04.title" }),
      text: intl.formatMessage({ id: "level.slider.m_04.text" }),
    },
    {
      img: Medal_58URL,
      title: intl.formatMessage({ id: "level.slider.m_05.title" }),
      text: intl.formatMessage({ id: "level.slider.m_05.text" }),
    },
    {
      img: Medal_64URL,
      title: intl.formatMessage({ id: "level.slider.m_06.title" }),
      text: intl.formatMessage({ id: "level.slider.m_06.text" }),
    },
    {
      img: Medal_70URL,
      title: intl.formatMessage({ id: "level.slider.m_07.title" }),
      text: intl.formatMessage({ id: "level.slider.m_07.text" }),
    },
  ];
};
