import React, { FC, useEffect } from "react";
import { useGetLocale } from "providers";
import { ReactComponent as CloseIcon } from "assets/icons/close-mui.svg";

import { Modal } from "components";

import { User, Medals, Statistic } from "./components";
import css from "./style.module.scss";
import { useLocation } from "react-router-dom";
import { ProfileData } from "pages/ModalPages/pages/Profile";
import clsx from "clsx";

interface Props {
  open: boolean;
  onClose: () => void;
  profile?: ProfileData;
}

export const UserModal: FC<Props> = ({ open, onClose, profile }) => {
  const intl = useGetLocale();
  const { search } = useLocation();

  useEffect(() => {
    if (!!search) {
      onClose();
    }
  }, [search]);

  return (
    <>
      <Modal
        open={open}
        onClose={onClose}
        withCloseIcon
        closeIcon={(props) => (
          <CloseIcon
            {...props}
            className={clsx(css.closeIcon, props.className)}
          />
        )}
        paper={{
          className: css.modalRoot,
        }}>
        <div className={css.root}>
          <div className={css.header}>
            <h1 className={css.title}>
              {intl.formatMessage({ id: "level.user.title" })}
            </h1>
          </div>
          <div className={css.scrollbar}>
            <User
              avatar={profile?.profile_picture_url}
              rating={profile?.level}
            />
            <Medals />
            <Statistic
              bets={profile?.bets}
              wagered={profile?.total_wagered}
              wins={profile?.profit_ath}
            />
          </div>
        </div>
      </Modal>
    </>
  );
};

export default UserModal;
