import React, { FC } from "react";

import { Avatar, Rating } from "components";
import { useUser } from "providers";

import css from "./style.module.scss";
import { ProfileData } from "pages/ModalPages/pages/Profile";

interface Props {
  profile?: ProfileData;
}

const Header: FC<Props> = ({ profile }) => {
  const { user } = useUser();

  return (
    <div className={css.root}>
      <Avatar
        avatar={profile?.profile_picture_url}
        userName={user?.user_name || "null"}
        className={css.avatar}
      />

      <div className={css.nameWrapper}>
        <p className={css.name}>{user?.user_name || "user name"}</p>
        <div>
          <Rating value={Number(profile?.level)} size="medium" />
        </div>
      </div>
    </div>
  );
};

export default Header;
