import React, { FC, useMemo } from "react";
import { useGetLocale } from "providers";
import clsx from "clsx";

import redChestUrl from "assets/levels/chests/red/chest_red_golden.webp";
import redChestClosedUrl from "assets/levels/chests/red/chest_red_closed.webp";
import greenChestUrl from "assets/levels/chests/green/chest_green_golden.webp";
import greenChestClosedUrl from "assets/levels/chests/green/chest_green_closed.webp";
import blueChestUrl from "assets/levels/chests/blue/chest_blue_Golden.webp";
import blueChestClosedUrl from "assets/levels/chests/blue/chest_blue_closed.webp";
import yelloChestUrl from "assets/levels/chests/yello/chest_yello_golden.webp";
import yelloChestClosedUrl from "assets/levels/chests/yello/chest_yello_closed.webp";
import coinsUrl from "assets/levels/medals&coins/golden/coins-golden.svg";

import { Rating } from "components";

import { ProfileData } from "pages/ModalPages/pages/Profile";

import css from "./style.module.scss";

interface Props {
  level: string;
  credit: string;
  total: number;
  status?: string;
  data?: ProfileData;
}

const TableItem: FC<Props> = ({ level = "0", credit, total, data }) => {
  const userLevel = Number(data?.level);
  const isOpen = userLevel >= Number(level);
  const intl = useGetLocale();

  const imgUrl = useMemo(() => {
    switch (Number(level) % 10) {
      case 1:
        return isOpen ? redChestUrl : redChestClosedUrl;
      case 0:
        return isOpen ? blueChestUrl : blueChestClosedUrl;

      case 7:
        return isOpen ? yelloChestUrl : yelloChestClosedUrl;

      case 4:
        return isOpen ? greenChestUrl : greenChestClosedUrl;

      default:
        return coinsUrl;
    }
  }, [level]);

  return (
    <div
      className={clsx(css.row, {
        [css.rowActive]: isOpen,
      })}>
      <p className={css.text}>{level}</p>
      <p className={css.text}>{total.toFixed(0)} USD</p>

      <p className={css.text}>
        {isOpen
          ? intl.formatMessage({ id: "level.vip.table.content.progress.ok" })
          : intl.formatMessage({
              id: "level.vip.table.content.progress.nOk",
            })}
      </p>
      <p className={css.text}>
        <Rating value={Number(level)} size={"medium"} />
      </p>
      <img src={imgUrl} alt="bonus" className={css.img} />
    </div>
  );
};

export default TableItem;
