import React, { FC, useMemo } from "react";
import { useGetLocale } from "providers";
import AutoSizer from "react-virtualized-auto-sizer";
import { VariableSizeList as List } from "react-window";

import { LevelItem } from "pages/Levels";
import { ProfileData } from "pages/ModalPages/pages/Profile";
import { SliderBlock } from "pages/Levels/components/InfoBlock/components";

import VirtualizedItem from "./VirtualizedItem";
import TableItem from "./TableItem";
import css from "./style.module.scss";

interface Props {
  list: LevelItem[];
  profile?: ProfileData;
}

export const Mobile: FC<Props> = ({ list, profile }) => {
  const intl = useGetLocale();

  const components = useMemo(() => {
    const tableElements = list.map(
      ({ credit_amount, level, total_wagered_required }) => (
        <TableItem
          level={level}
          total={total_wagered_required}
          credit={credit_amount}
          data={profile}
        />
      ),
    );

    return [
      <div className={css.header}>
        <h1 className={css.title}>
          {intl.formatMessage({ id: "level.vip.button" })}
        </h1>
      </div>,

      <div className={css.sliderWrapper}>
        <SliderBlock />
      </div>,

      <div className={css.row}>
        <p className={css.headerItem}>
          {intl.formatMessage({ id: "level.vip.table.header.level" })}
        </p>

        <p className={css.headerItem}>
          {intl.formatMessage({ id: "level.vip.table.header.total" })}
        </p>

        <p className={css.headerItem}>
          {intl.formatMessage({ id: "level.vip.table.header.star" })}
        </p>
        <p className={css.headerItem}>
          {intl.formatMessage({ id: "level.vip.table.header.bonus" })}
        </p>
      </div>,
      ...tableElements,
    ];
  }, [list, profile]);

  return (
    //@ts-ignore
    <AutoSizer>
      {({ width, height }) => {
        return (
          <List
            height={height}
            width={width - 5}
            overscanCount={10}
            itemCount={components.length}
            itemData={components}
            itemSize={(i) => {
              switch (i) {
                case 0:
                  return 27 + 60;

                case 1:
                  return 500;

                case 2:
                  return 54;

                default:
                  return 53;
              }
            }}>
            {VirtualizedItem}
          </List>
        );
      }}
    </AutoSizer>
  );
};
