import React, { FC, useEffect, useState } from "react";
import { useGetLocale } from "providers";
import clsx from "clsx";

import css from "./style.module.scss";
import { ProfileData } from "pages/ModalPages/pages/Profile";
import { LevelItem } from "pages/Levels";

interface Props {
  profile?: ProfileData;
  levels?: LevelItem[];
}

const RewardData: FC<Props> = ({ levels, profile }) => {
  const intl = useGetLocale();

  const [profileParams, setProfileParams] = useState({
    actualLevel: 0,
    xp: 0,
  });
  const [reach, setReach] = useState(100);

  useEffect(() => {
    if (profile) {
      setProfileParams({
        actualLevel: Number(profile.level),
        xp: Number(profile.total_wagered),
      });
    }
  }, [profile]);

  useEffect(() => {
    if (levels && !!levels.length) {
      const lvl =
        profileParams.actualLevel < levels.length
          ? profileParams.actualLevel
          : levels.length - 1;
      setReach(levels[lvl].total_wagered_required);
    }
  }, [levels, profileParams]);

  return (
    <div className={css.root}>
      <span className={css.textFlexWrapper}>
        <p className={css.text}>
          {intl.formatMessage({ id: "level.userData.current" })}:
          <span className={css.textSpan}>
            {` ${Math.round(profileParams.xp)} XP`}
          </span>
        </p>

        <span className={clsx(css.text, css.separator)}>{` / `}</span>

        <p className={css.text}>
          {intl.formatMessage({ id: "level.userData.total" })}:
          <span className={css.textSpan}> {profileParams.xp} USD </span>
        </p>
      </span>

      {!!(Number(profileParams.actualLevel) < 70) && (
        <p className={css.text}>
          {intl.formatMessage({ id: "level.userData.reachTo.reach" })}

          <span className={css.textSpan}>
            {` ${Math.round(reach - profileParams.xp)} XP `}
          </span>

          {intl.formatMessage({ id: "level.userData.reachTo.to" })}

          <span className={css.textSpan}>
            {` LVL ${profileParams.actualLevel + 1}`}
          </span>
        </p>
      )}
    </div>
  );
};

export default RewardData;
