import React, { FC, useEffect, useMemo, useState } from "react";

import { Avatar, Rating } from "components";
import { useUser } from "providers";
import { MedalsProps } from "assets/levels/type";

import css from "./style.module.scss";

interface Props {
  avatar?: string;
  rating?: string;
}

export const User: FC<Props> = ({ avatar, rating = "70" }) => {
  const { user } = useUser();

  const [medal, setMedal] = useState<{
    Component: FC<MedalsProps> | null;
  }>({
    Component: null,
  });

  useEffect(() => {
    const getMedal = async () => {
      const N = Number;
      const { medalTextComponentsConfig } = await import(
        "assets/levels/medalComponents"
      );

      const sortedMedals = Object.entries(medalTextComponentsConfig)
        .sort((a, b) => N(a[0]) - N(b[0]))
        .reverse();

      for (const [key, url] of sortedMedals) {
        if (N(rating) > N(key)) {
          setMedal({
            Component: url,
          });
        }
      }

      setMedal({
        Component: medalTextComponentsConfig["0"],
      });
    };

    getMedal();
  }, [rating]);

  return (
    <div className={css.root}>
      <div className={css.rewardWrapper}>
        <Avatar
          avatar={avatar}
          userName={user?.user_name || "null"}
          className={css.avatar}
        />
        <span className={css.rewardImgWrapper}>
          <span className={css.rewardImg}>
            {medal.Component && <medal.Component value={rating} />}
          </span>
        </span>
      </div>

      <p className={css.name}>{user?.user_name}</p>

      <div className={css.ratingWrapper}>
        <Rating value={Number(rating)} size="medium" />
      </div>
    </div>
  );
};
