import React, { FC } from "react";
import { useGetLocale } from "providers";
import { ReactComponent as Arrow } from "assets/icons/keyboard-arrow-right.svg";

import { ProfileData } from "pages/ModalPages/pages/Profile";
import { LevelItem } from "pages/Levels";

import Header from "./Header";
import Medal from "./Medal";
import ProgressBar from "./ProgressBar";
import css from "./style.module.scss";

interface Props {
  handleOpenModal: () => void;
  profile?: ProfileData;
  levels?: LevelItem[];
}

export const UserBlock: FC<Props> = ({ handleOpenModal, profile, levels }) => {
  const intl = useGetLocale();

  return (
    <div className={css.root}>
      <div className={css.header}>
        <Header profile={profile} />
        <Medal profile={profile} />
      </div>
      <ProgressBar levels={levels} profile={profile} />

      <span className={css.link} onClick={handleOpenModal}>
        {intl.formatMessage({ id: "wallet.records.details" })}
        <Arrow />
      </span>
    </div>
  );
};
