import React, { useMemo } from "react";

const VirtualizedItem = (props) => {
  const { data, index, style } = props;

  const Component = useMemo(() => () => data[index], [index]);

  return (
    <div style={{ ...style }}>
      <Component />
    </div>
  );
};

export default React.memo(VirtualizedItem, (p, n) => p.index === n.index);
