import React, { FC } from "react";
import { useGetLocale } from "providers";

import { ReactComponent as LockIcon } from "assets/icons/lock.svg";
import { ReactComponent as LockOpenIcon } from "assets/icons/lock-open.svg";

import css from "./style.module.scss";

interface Props {
  img: string;
  name: string;
  text: string;
  unlocked: boolean;
}

const Card: FC<Props> = ({ img, name, text, unlocked }) => {
  const intl = useGetLocale();

  const HeaderContent = () =>
    unlocked ? (
      <span className={css.lockWrapper}>
        <LockOpenIcon /> {intl.formatMessage({ id: "level.rights.unlocked" })}
      </span>
    ) : (
      <span className={css.lockWrapper}>
        <LockIcon />
        {intl.formatMessage({ id: "level.rights.locked" })}
      </span>
    );
  return (
    <div className={css.root} style={!unlocked ? { opacity: 0.6 } : {}}>
      <div className={css.header}>
        <HeaderContent />
      </div>
      <div className={css.body}>
        <img src={img} alt={name} className={css.img} />
        <div className={css.textWrapper}>
          <h4 className={css.title}>{name}</h4>
          <p className={css.text}>{text}</p>
        </div>
      </div>
    </div>
  );
};

export default Card;
