import React, { FC, useMemo, useRef } from "react";
import { useGetLocale } from "providers";
import AutoSizer from "react-virtualized-auto-sizer";
import { FixedSizeList as List } from "react-window";

import { CircularProgress } from "components";

import { LevelItem } from "pages/Levels";
import { ProfileData } from "pages/ModalPages/pages/Profile";

import VirtualizedItem from "./TableItem/VirtualizedItem";
import css from "./style.module.scss";

interface Props {
  list: LevelItem[];
  data: ProfileData | undefined;
}

export const Table: FC<Props> = ({ list, data }) => {
  const intl = useGetLocale();

  const rootRef = useRef<HTMLDivElement>(null);

  const tableHeight = useMemo(() => {
    if (rootRef.current) {
      const rootHeight = rootRef.current.offsetHeight;
      //@ts-ignore
      const headerHeight: number = rootRef.current.firstChild.offsetHeight || 0;

      return rootHeight - headerHeight;
    }

    return 250;
  }, [rootRef?.current]);

  return (
    <div className={css.root} ref={rootRef}>
      <div className={css.row}>
        <p className={css.headerItem}>
          {intl.formatMessage({ id: "level.vip.table.header.level" })}
        </p>
        {/* <p className={css.headerItem}>
          {intl.formatMessage({ id: "level.vip.table.header.credit_amount" })}
        </p> */}
        <p className={css.headerItem}>
          {intl.formatMessage({ id: "level.vip.table.header.total" })}
        </p>
        <p className={css.headerItem}>
          {intl.formatMessage({ id: "level.vip.table.header.xpProgress" })}
        </p>
        <p className={css.headerItem}>
          {intl.formatMessage({ id: "level.vip.table.header.star" })}
        </p>
        <p className={css.headerItem}>
          {intl.formatMessage({ id: "level.vip.table.header.bonus" })}
        </p>
      </div>

      <div className={css.table} style={{ height: tableHeight }}>
        <AutoSizer>
          {({ height, width }) => {
            return (
              <List
                height={height}
                width={width - 5}
                overscanCount={10}
                itemCount={list.length}
                itemData={{ list, data }}
                itemSize={53}>
                {VirtualizedItem}
              </List>
            );
          }}
        </AutoSizer>

        {!list.length && (
          <div className={css.loaderWrapper}>
            <CircularProgress size={75} />
          </div>
        )}
      </div>
    </div>
  );
};
