import React, { useState, FC, useEffect } from "react";

import { LevelItem } from "pages/Levels";
import { ProfileData } from "pages/ModalPages/pages/Profile";

import RewardData from "./RewardData";
import RewardList from "./RewardList";
import css from "./style.module.scss";

interface Props {
  profile?: ProfileData;
  levels?: LevelItem[];
}

const ProgressBar: FC<Props> = ({ levels, profile }) => {
  const [actualRewardX, setActualRewardX] = useState(0);
  const [progressLevel, setProgressLevel] = useState(0);

  useEffect(() => {
    if (profile) {
      const level = Number(profile.level) < 70 ? Number(profile.level) % 10 : 9;
      setProgressLevel(level);
    }
  }, [profile]);

  return (
    <div className={css.root}>
      <div>
        <RewardList setActualRewardX={setActualRewardX} level={progressLevel} />
      </div>

      <div className={css.rewardLine}>
        <div
          className={css.rewardArrow}
          style={{ transform: `translateX(${actualRewardX}px)` }}
        />
      </div>

      <RewardData
        {...{
          levels,
          profile,
        }}
      />
    </div>
  );
};

export default ProgressBar;
