import React, { FC, useCallback } from "react";

import { useGetLocale } from "providers";
import { ReactComponent as ArrowRight } from "assets/icons/chevron-right.svg";
import { Total } from "./Total";
import { useModalLocation } from "hooks";

import css from "./style.module.scss";

interface Props {
  wins?: string;
  bets?: string;
  wagered?: string;
}

export const Statistic: FC<Props> = ({ bets = 0, wagered = 0, wins = 0 }) => {
  const intl = useGetLocale();

  const { pushHistoryTo, location } = useModalLocation("profile");

  const handleOpenData = useCallback(() => {
    pushHistoryTo({});
  }, [location]);

  return (
    <div className={css.root}>
      <div className={css.header}>
        <h4 className={css.title}>
          {intl.formatMessage({ id: "level.user.statistics.name" })}
        </h4>

        <p className={css.link} onClick={handleOpenData}>
          {intl.formatMessage({
            id: "wallet.records.details",
          })}
          <ArrowRight />
        </p>
      </div>

      <div className={css.content}>
        <Total
          title={intl.formatMessage({ id: "level.user.statistics.table.wins" })}
          value={`${wins} USD`}
        />
        <Total
          title={intl.formatMessage({ id: "level.user.statistics.table.bets" })}
          value={`${bets}`}
        />
        <Total
          title={intl.formatMessage({
            id: "level.user.statistics.table.wagered",
          })}
          value={`${wagered} USD`}
        />
      </div>
    </div>
  );
};
