import React, { FC } from "react";

import { LevelItem } from "pages/Levels";
import { ProfileData } from "pages/ModalPages/pages/Profile";

import { AboutVip, SliderBlock, UserBlock } from "./components";
import css from "./style.module.scss";

interface Props {
  handleOpenVipModal: () => void;
  handleOpenUserModal: () => void;

  profile?: ProfileData;
  levels?: LevelItem[];
}

export const InfoBlock: FC<Props> = ({
  handleOpenVipModal,
  handleOpenUserModal,
  profile,
  levels,
}) => {
  return (
    <div className={css.root}>
      <SliderBlock />
      <div className={css.subPart}>
        <UserBlock
          {...{
            profile,
            levels,
            handleOpenModal: handleOpenUserModal,
          }}
        />
        <AboutVip handleOpenModal={handleOpenVipModal} />
      </div>
    </div>
  );
};
