import React from "react";
import { useGetLocale } from "providers";
import { Button } from "components";

import css from "./style.module.scss";
import { FC } from "react";

interface Props {
  handleOpenModal: () => void;
}

export const AboutVip: FC<Props> = ({ handleOpenModal }) => {
  const intl = useGetLocale();

  return (
    <div className={css.root}>
      <div className={css.textBlock}>
        <h1 className={css.title}>
          {intl.formatMessage({ id: "level.vip.title" })}
        </h1>

        <p className={css.text}>
          {intl.formatMessage({ id: "level.vip.text" })}
        </p>
      </div>

      <Button className={css.button} onClick={handleOpenModal}>
        {intl.formatMessage({ id: "level.vip.button" })}
      </Button>
    </div>
  );
};
