import React, { FC } from "react";
import clsx from "clsx";

import { Modal } from "components";
import { useWindowSize } from "providers";

import { ReactComponent as CloseIcon } from "assets/icons/close-mui.svg";
import { LevelItem } from "pages/Levels";
import { ProfileData } from "pages/ModalPages/pages/Profile";

import { Desktop, Mobile } from "./components/";
import css from "./style.module.scss";

interface Props {
  open: boolean;
  onClose: () => void;
  list: LevelItem[];
  profile: ProfileData | undefined;
}

export const VipModal: FC<Props> = ({ open, onClose, list, profile }) => {
  const { width } = useWindowSize();

  return (
    <>
      <Modal
        open={open}
        onClose={onClose}
        withCloseIcon
        closeIcon={(props) => {
          return (
            <CloseIcon
              {...props}
              className={clsx(css.closeIcon, props?.className)}
            />
          );
        }}
        paper={{
          className: css.modalRoot,
        }}>
        <div className={css.root}>
          <div className={css.wrapper}>
            {width >= 600 ? (
              <Desktop {...{ list, profile }} />
            ) : (
              <Mobile {...{ onClose, list, profile }} />
            )}
          </div>
        </div>
      </Modal>
    </>
  );
};

export default VipModal;
