import React, { FC, useEffect, useRef, useState } from "react";

import { useWindowSize } from "providers";

import { rewardList } from "./rewards";
import css from "./style.module.scss";

interface Props {
  setActualRewardX: React.Dispatch<React.SetStateAction<number>>;
  level: number;
}

const arrowSize = 12;

const RewardList: FC<Props> = ({ setActualRewardX, level }) => {
  const rootRef = useRef<HTMLDivElement>(null);
  const activeElementRef = useRef<HTMLImageElement>(null);
  const { width } = useWindowSize();

  const [rootWidth, setRootWidth] = useState(0);

  useEffect(() => {
    if (rootRef.current) {
      const rootWidth = rootRef.current.offsetWidth;
      setRootWidth(rootWidth);

      if (activeElementRef.current) {
        const elementWidth = rootWidth / 10;

        setActualRewardX(elementWidth * level + elementWidth / 2 - arrowSize);
      }
    }
  }, [activeElementRef.current, rootRef.current, width, level]);

  return (
    <div className={css.root} ref={rootRef}>
      {rewardList.map((url, i) => {
        const isOpen = level >= Number(i);

        const link = url[1] ? (isOpen ? url[0] : url[1]) : url[0];

        return (
          <img
            src={link}
            alt="reward"
            className={css.img}
            style={{
              opacity: !isOpen ? 0.3 : 1,
              maxWidth: !!rootWidth ? rootWidth / 10 : "",
            }}
            ref={i === level ? activeElementRef : null}
          />
        );
      })}
    </div>
  );
};

export default RewardList;
