import React from "react";
import { FC } from "react";

import css from "./style.module.scss";

interface Props {
  title: string;
  text: string;
}

const SliderText: FC<Props> = ({ title, text }) => {
  return (
    <div>
      <h1 className={css.title}>{title}</h1>
      <p className={css.text}>{text}</p>
    </div>
  );
};

export default SliderText;
