import React from "react";
import { FC } from "react";

import css from "./style.module.scss";

interface Props {
  title: string;
  value: string;
}

export const Total: FC<Props> = ({ title, value }) => {
  return (
    <div className={css.root}>
      <p className={css.title}>{title}</p>
      <p className={css.value}>{value}</p>
    </div>
  );
};
