import React from "react";
import { useGetLocale } from "providers";
import clsx from "clsx";

import Card from "./Card";
import Exclusive from "./Exclusive";
import { useCards } from "./config";
import css from "./style.module.scss";

export const Rights = () => {
  const intl = useGetLocale();
  const { cards, exclusive } = useCards();

  return (
    <div className={css.root}>
      <h1 className={css.title}>
        {intl.formatMessage({ id: "level.rights.title" })}
      </h1>
      <div className={clsx(css.wrapper, css.cards)}>
        {cards.map(({ img, name, text, unlocked }) => {
          return <Card img={img} name={name} text={text} unlocked={unlocked} />;
        })}
      </div>

      {/* <h1 className={css.title}>
        {intl.formatMessage({ id: "level.exclusive.title" })}
      </h1>
      <div className={clsx(css.wrapper, css.exclusive)}>
        {exclusive.map(({ title, text, img, color }) => {
          return (
            <Exclusive title={title} text={text} img={img} color={color} />
          );
        })}
      </div> */}
    </div>
  );
};
